@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  @apply bg-gray-100;
}

.main-card-title {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}

.cart-card-title {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  line-clamp: 1;
  -webkit-box-orient: vertical;
}

.cart-button {
  @apply bg-rose-600 text-white sm:px-5 font-roboto sm:mx-4 border-2
  border-rose-600 rounded-md active:bg-white active:text-rose-600 
  active:border-2 transition-all hover:bg-rose-500 hover:border-rose-500
  px-3 mx-3;
}

.main-card-shadow {
  box-shadow: -2px 2px 10px 2px #a6abb44f;
}

.main-card-shadow-rose {
  box-shadow: -2px 2px 10px 2px #e11d4733;
}

.font-roboto {
  font-family: "Roboto", sans-serif;
}

.modal-shadow {
  box-shadow: -2px 2px 10px 3px #47494d7e;
}

.modal {
  transition: right 0.3s ease-out;
}

.right-700- {
  right: -700px;
}

.right-0 {
  right: 0;
}

.text-input {
  @apply border-[1px] border-neutral-400 rounded-sm pl-2 mr-2
focus:outline-rose-600;
}

.btn-bordered {
  @apply border-rose-600 border-2 px-2 font-roboto font-semibold
text-rose-600 rounded-md py-1 hover:bg-rose-600 hover:text-white
  transition-all disabled:border-rose-300 disabled:text-rose-300
  disabled:hover:bg-white disabled:hover:text-rose-300;
}

.fancy-label {
  @apply scale-90 font-roboto transition-all;
}

.fancy-input:placeholder-shown + .fancy-label {
  @apply translate-y-8 translate-x-2 scale-100 text-neutral-500;
}

.fancy-input {
  @apply py-2 font-roboto;
}

.fancy-input:focus + .fancy-label {
  @apply text-rose-600 translate-y-0 translate-x-0 scale-90;
}

.center {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
